
body{
  background: linear-gradient(to right, rgba(10, 6, 218, 0.767), rgba(6, 1, 14, 0.849));
}
button {
  margin: 20% 0% 0% 50% ;
  position:absolute
}
.dropDown{
  width: fit-content;
  position: absolute;
  padding: 5% 0% 0% 50% ;
}

/* class to show when the bottom is selected */
.tooltip-bottom{
  position: absolute;
  background-color: rgb(238, 255, 0);
  margin: 22% 0% 0% 46%;
  height:50px;
  text-align: center;
  /* padding-top:20px; */
  width: 160px;
  .tip {
  width: 0px;
  height: 0px;
  position: absolute;
  background: transparent;
  border: 10px solid rgb(238, 255, 0);
  }
  .tip-bottom {
  top: -20px; 
  margin-left:45%;
  border-right-color: transparent;
  border-left-color: transparent;
  border-top-color: transparent;
  }
}


